<template>
  <div class="indexText">
    <i class="btn custom-icon" @click="goBack" ></i> 
    <!-- 左侧部分，包含图片 -->
    <div class="left">
      <img :src="getImageUrl(userObj.imgUrl)" :width="userObj.width1" :height="userObj.height"  />
    </div>

    <!-- 右侧部分，包含标题和段落 -->
    <div class="right">
      <h1 class="title">{{ userObj.title }}</h1>
      <p class="paragraph">{{ userObj.text }}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "indexText",
  data() {
    return {
      userObj: {},
    };
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    // 获取存储的字符串
    const storedUser = sessionStorage.getItem("data");

    if (storedUser) {
      // 将字符串解析为对象
      this.userObj = JSON.parse(storedUser);
      console.log(this.userObj); // { name: "John", age: 30 }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1); // 返回上一个页面
    },
    getImageUrl(imgPath) {
      // 在 Vue 项目中，通过 require() 动态加载图片log
      return require(`@/assets/${imgPath}`);
    },
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  computed: {}, // 计算属性
};
</script>
 
<style scoped >
.indexText {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between; /* 左右分布 */
  align-items: center; /* 垂直居中 */
  padding: 20px;
  background:  #f2f2f2; /* 容器的背景色 */
  color: white;
  color: #000;
}

.left {
  /* border: 1px solid #000; */
  flex: 2; /* 使左侧部分占用剩余空间 */
  width: 100%;
  height: 100%;
  padding: 60px 60px 60px 40px;
}

.left img {
  display: block;
  max-width: 100%;  /* 限制最大宽度为父元素宽度 */
  height: auto; 
  margin: 0 auto;    /* 自动调整高度以保持宽高比 */
}

.right {
  flex: 1; /* 使右侧部分占用更多空间 */
  padding: 10px;
  height: 100%;
  padding: 40px 60px 60px 20px;
  /* border: 1px solid #000; */
}

.title {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 20px;
}

.paragraph {
  font-size: 22px;
  line-height: 1.5;
  width: 100%;
  height: 88%;
  overflow: hidden;
  text-align: justify;
}
.btn{
  position: absolute;
  top: 48%;
  left: 40px;
}
.custom-icon {
  background: url('../assets/back/go4.png') 
  no-repeat center center;
  width: 80px; /* 设置按钮图标大小 */
  height: 50px;
  background-size: 200% 200%; 
  object-fit: contain; /* 确保图片按比例自适应 */
  z-index: 99999;
  margin-left: -15px;
}
</style>


